/* eslint-disable no-unused-vars */
/* eslint-disable no-param-reassign */
import { modal, ModalsContainer } from './components/vu-modal';
import { message, MessageContainer } from './components/vu-message';
import directive from './directives/index';

import './styles/index.scss';

// Auto imports all vue components
const req = require.context('./components', true, /.ue$/);
const components = req.keys()
  // except ModalsContainer
  .filter((key) => !key.includes('modal-container') && !key.includes('message-container'))
  .map(req);

// eslint-disable-next-line no-unused-vars
const install = (Vue, config) => {
  components.forEach((el) => {
    Vue.component(el.default.name, el.default);
  });

  Vue.component('vu-modal-container', ModalsContainer); 
  Vue.component('vu-message-container', MessageContainer); 

  directive.install(Vue, config);
};

const plugin = { install };
export default install;
export { 
  install, plugin, modal, message,
};
