export default {
  props: {
    modelValue: {
      type: [Object, String, Number, Array, Boolean, Date],
      default: () => '',
    },
    label: {
      type: String,
      default: () => '',
    },
    type: {
      type: String,
      default: () => 'text',
    },
    helper: {
      type: String,
      default: () => '',
    },
    placeholder: {
      type: String,
      default: () => '',
    },
    options: {
      type: Array,
      default: () => [],
    },
  },
  emits: ['update:modelValue'],
  computed: {
    value() {
      return this.modelValue;
    },
  },
};
